import * as React from 'react';
import { Controller } from '@ali/4ever-cangjie';
import { selectMention } from '../actions';
import type { MobileToolbarButtonProps } from '@ali/4ever-component';

export interface MentionButtonProps {
  controller: Controller;
  locale: Record<string, string>;
}

const isDisabled = (controller: Controller) => {
  const { isCollapsedAtEdge } = controller.value.selection;
  return isCollapsedAtEdge;
};

export function MobileMentionButton(props: MobileToolbarButtonProps) {
  const { controller, Wrapper, item } = props;
  const handleClick = () => controller.run('onAction', selectMention());

  return (
    <Wrapper
      type="big"
      icon="at"
      item={item}
      isDisabled={isDisabled(controller)}
      onClick={handleClick}
    />
  );
}

export function MobileMentionInsertButton(props: MobileToolbarButtonProps) {
  const { controller, Wrapper, item, locale } = props;
  const handleClick = () => controller.run('onAction', selectMention());

  return (
    <Wrapper
      type="line"
      icon="at"
      item={item}
      isDisabled={isDisabled(controller)}
      lineText={locale?.buttonTitle}
      onClick={handleClick}
    />
  );
}
